import {getQusetions, getAllSubject, getAllQuestionsType, getAllExamType} from '@/service/questionsManger'
export default {
    namespace:'watchQuestions',
    state:{
        questions:[],
        subject: [],
        questionsType: [],
        examType: []
    },
    reducers:{
        changeState (state, payload) {
            return {...state, ...payload}
        }
    },
    effects:{
        *getQuestions(payload, {call,put}){
            console.log(payload.filter)
            let result = yield call(getQusetions, payload.filter);
            if(result.code === 1){
                yield put({type:'changeState',questions:result.data});
            }
        },
        *getAllSubject(payload, {call, put}) {
            let result = yield call(getAllSubject);
            if(result.code === 1){
                yield put({type:'changeState',subject:result.data});
            }
        },
        *getQuestionsType(payload, {call,put}) {
            let result = yield call(getAllQuestionsType);
            if(result.code === 1){
                yield put({type:'changeState',questionsType:result.data});
            }
        },
        *getAllExamType(payload, {call, put}) {
            let result = yield call(getAllExamType);
            if(result.code === 1){
                yield put({type:'changeState',examType:result.data});
            }
        }
    }
}